import { useState, useEffect } from "react";
import Layout from "../components/LayoutComponent";
import WeeklySummary from "../components/WeeklySummaryComponent";
import CombinedChart from "../components/CombinedChartComponent"; // Membuat komponen grafik gabungan
import SavingsPrediction from "../components/SavingsPredictionComponent";
import { IncomeData } from "../interfaces/IIncomingData"; // Mengimpor interface pemasukan
import { ExpenseData } from "../interfaces/IExpenseData"; // Mengimpor interface pengeluaran

const Dashboard = () => {
  const [incomeData, setIncomeData] = useState<IncomeData[]>([]); // Tipe data pemasukan
  const [expenseData, setExpenseData] = useState<ExpenseData[]>([]); // Tipe data pengeluaran
  const savings = 500;

  useEffect(() => {
    const fetchIncomeData = async () => {
      try {
        const response = await fetch("/api/incomes");
        const data = await response.json();
        setIncomeData(data);
      } catch (error) {
        console.error("Error fetching income data:", error);
      }
    };

    const fetchExpenseData = async () => {
      try {
        const response = await fetch("/api/expenses");
        const data = await response.json();
        setExpenseData(data);
      } catch (error) {
        console.error("Error fetching expense data:", error);
      }
    };

    fetchIncomeData();
    fetchExpenseData();
  }, []);

  return (
    <Layout>
      <h2 className="text-3xl font-bold mb-4 text-center">Dashboard</h2>
      <p className="text-lg text-center mb-6 text-gray-700">
        Selamat datang di dashboard aplikasi penghitung pengeluaran dan
        pemasukan!
      </p>

      <div className="mb-8">
        <WeeklySummary />
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">
          Grafik Pemasukan dan Pengeluaran
        </h3>
        <CombinedChart incomeData={incomeData} expenseData={expenseData} />
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">Prediksi Tabungan</h3>
        <SavingsPrediction savings={savings} />
      </div>
    </Layout>
  );
};

export default Dashboard;
